<template>
  <div
    v-if="!upperLevel || isHideNav"
    class="content-header-single"
  >
    <slot name="header">
      {{ currentLevel }}
    </slot>
  </div>
  <div
    v-else
    class="content-header-both"
  >
    <div class="back-link">
      <router-link :to="backPath">
        <gs-button
          class="back-btn"
          type="primary"
          @click="backBtnClick"
        >
          <i class="gs-icon gs-icon-revoke" />
        </gs-button>
      </router-link>
    </div>
    <div class="nav-name">
      <div class="current-nav">
        {{ currentLevel }}
      </div>
      <div class="upper-nav">
        {{ upperLevel }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'BreadCrumb',
  props: {
    currentLevel: {
      type: String,
      required: true
    },
    upperLevel: {
      type: String,
      default: ''
    },
    backPath: {
      type: String,
      default: ''
    }
  },
  data() {
    return {};
  },
  computed: {
    isHideNav() {
      return !!this.$router.currentRoute.query.nav;
    }
  },
  methods: {
    backBtnClick() {
      this.$emit('back');
    }
  }
};
</script>
