<template>
  <div class="api-publish-page">
    <div class="content-header">
      <bread-crumb
        current-level="API发布"
        upper-level="数据分享"
        back-path="/main/data-share"
      />
    </div>
    <div class="api-publish-body">
      <div class="publish-content">
        <div class="title-text">
          发布API列表：
        </div>
        <el-table
          :data="list"
          class="api-table"
        >
          <el-table-column
            label="序号"
            type="index"
          />
          <el-table-column
            label="名称"
            prop="name"
          />
          <el-table-column
            label="分组"
            prop="groupName"
          />
          <el-table-column
            label="版本"
            prop="version"
          />
          <el-table-column
            label="操作"
          >
            <template slot-scope="scope">
              <gs-button
                type="text-primary"
                icon="close"
                @click="delApi(scope)"
              />
            </template>
          </el-table-column>
        </el-table>
        <hr class="split-line">
        <gs-checkbox
          v-model="checked"
          class="confirm-checkbox"
        >
          我已检查以上内容并了解发布影响，确认操作
        </gs-checkbox>
        <div class="btn-group">
          <gs-button
            :disabled="!checked || !list.length"
            :loading="loading"
            type="primary"
            @click="publishApi"
          >
            确定
          </gs-button>
          <gs-button
            type="default"
            @click="back"
          >
            取消
          </gs-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BreadCrumb from 'src/components/breadcrumb';
import * as service from 'src/service/data-share/index';
import './style.scss';

export default {
  components: {
    BreadCrumb
  },

  data() {
    return {
      list: [],
      loading: false,
      checked: false
    };
  },

  mounted() {
    const api = localStorage && localStorage.getItem('SELECTED_API');
    this.list = api ? JSON.parse(api) : [];
  },

  methods: {
    delApi(scope) {
      this.$Modal.confirm({
        title: '提示',
        content: '确定移除该API吗？',
        onOk: () => {
          this.list.splice(scope.$index, 1);
          localStorage && localStorage.setItem('SELECTED_API', JSON.stringify(this.list));
          this.$Message.success('移除成功');
        }
      });
    },
    publishApi() {
      this.loading = true;
      const ids = this.list.map(i => i.guid);
      service.publishApi({ids, status: 1}).then(res => {
        this.loading = false;
        this.$Message.success('发布成功');
        localStorage && localStorage.removeItem('SELECTED_API');
        this.back();
      }).catch(() => {
        this.loading = false;
      });
    },
    back() {
      this.$router.push('/main/data-share');
    }
  }
};
</script>
